.App {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  background-color: #282c34;
  color: white;
}

.App-header {
  font-size: calc(10px + 2vmin);
}

.App-game {

}

.Key-table-wrapper{
  display: inline-flex;
  flex-direction: row;
}

.Key-table {
  margin-left: 1em;
  margin-right: 1em;
}

a {
  color: #61dafb;
}

button {
  padding: 1em;
  margin-left: 1em;
  margin-right: 1em;
}

.App-instructions {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  color: #ff7c7c;
}